import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, Box, IconButton, Drawer, List, ListItemButton, ListItemText, ListSubheader, Hidden } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import { Link as ScrollLink, Element } from 'react-scroll';
import logo from '../logo.png';
import './index.css';

const Header = () => {
  const [drawerOpen, setDrawerOpen] = useState(false);

  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  const anchorMenu = (
    <List
      sx={{
        width: 250, // Fixed width for tablet and larger screens
        minWidth: 250, // Ensure minimum width for the Drawer
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        '& .MuiListItemButton-root': {
          width: '100%', // Full width for each ListItemButton
          justifyContent: 'center', // Center content horizontally
          textAlign: 'center', // Center text horizontally
          //padding: '12px', // Adjust padding for touch area
          '&:hover': {
            backgroundColor: '#f5f5f5', // Change background color on hover
          },
        },
      }}
      role="presentation"
      onClick={toggleDrawer}
      onKeyDown={toggleDrawer}
      className="custom-menu"
    >

      <ScrollLink to="home" smooth={true} duration={500} offset={-60} onClick={toggleDrawer}>
        <ListItemButton component="a" className="custom-list-item-button" href="#">
          <ListItemText primary="Incio" />
        </ListItemButton>
      </ScrollLink>
      <ScrollLink to="about-me" smooth={true} duration={500} offset={-60} onClick={toggleDrawer}>
        <ListItemButton component="a" className="custom-list-item-button" href="#">
          <ListItemText primary="Sobre Mim" />
        </ListItemButton>
      </ScrollLink>
      <ScrollLink to="footer" smooth={true} duration={500} offset={-60} onClick={toggleDrawer}>
        <ListItemButton component="a" className="custom-list-item-button" href="#">
          <ListItemText primary="Contactos" />
        </ListItemButton>
      </ScrollLink>
    </List>
  );

  return (
    <>
      <AppBar position="fixed">
        <Toolbar>
          <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
            <img src={logo} alt="Logo" style={{ height: '60px', marginRight: '10px' }} />
            <Typography variant="h6" noWrap>
              Ninno Tattoos
            </Typography>
          </Box>
          <Hidden mdDown> {/* Hide on mobile and tablet */}
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              {/* Menu items for desktop */}
              <Box sx={{ display: 'flex', justifyContent: 'space-between', width: '300px' }}>

                <ScrollLink to="home" smooth={true} duration={500} offset={-80}>
                  <Typography variant="body1" sx={{ cursor: 'pointer' }}>
                    Incio
                  </Typography>
                </ScrollLink>
                <ScrollLink to="about-me" smooth={true} duration={500} offset={-60}>
                  <Typography variant="body1" sx={{ cursor: 'pointer' }}>
                    Sobre Mim
                  </Typography>
                </ScrollLink>
                <ScrollLink to="footer" smooth={true} duration={500} offset={-60}>
                  <Typography variant="body1" sx={{ cursor: 'pointer' }}>
                    Contactos
                  </Typography>
                </ScrollLink>
              </Box>
            </Box>
          </Hidden>
          <Hidden mdUp> {/* Hide on desktop */}
            <IconButton
              edge="end"
              color="inherit"
              aria-label="menu"
              sx={{ ml: 2 }}
              onClick={toggleDrawer}
            >
              <MenuIcon />
            </IconButton>
          </Hidden>
        </Toolbar>
      </AppBar>
      <Drawer
        anchor="right"
        open={drawerOpen}
        onClose={toggleDrawer}
        sx={{
          '& .MuiDrawer-paper': {
            width: '100%', // Full width of the viewport
            boxSizing: 'border-box', // Ensure consistent box sizing
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center', // Center drawer content vertically
            overflowX: 'hidden', // Ensure no horizontal overflow
            paddingTop: 0, // Remove top padding
            paddingBottom: 0, // Remove bottom padding
          },
          '& .MuiList-root': {
            width: '100%', // Ensure the List takes full width
          },
        }}
      >
        <Box sx={{ textAlign: 'right', width: '100%', padding: '5px 15px' }}>
          {/* Close button at the top right */}
          <IconButton
            edge="end"
            color="inherit"
            aria-label="close"
            onClick={toggleDrawer}
            sx={{ height: '100%' }} // Added marginRight and height styles
          >
            <CloseIcon />
          </IconButton>
        </Box>
        {anchorMenu}
      </Drawer>
    </>
  );
}

export default Header;
