import React from 'react';
import { Box, Container, Grid, Typography, Link, List, ListItem, ListItemText } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import { FaFacebook, FaInstagram, FaTwitter } from 'react-icons/fa';
import { isMobile } from 'react-device-detect';
import styled from 'styled-components';

const SocialIconsContainer = styled.div`
  display: flex;
  justify-content: normal;
  align-items: center;
  margin-top: 10px;
`;

const SocialIcon = styled.div`
  font-size: 2rem;
  margin: 0 10px;
  color: #333;
  transition: color 0.3s ease;
  cursor: pointer;

  &:hover {
    color: #007bff;
  }
`;

const FacebookIcon = styled(FaFacebook)`
  color: #1877f2;
`;

const InstagramIcon = styled(FaInstagram)`
  color: #c32aa3;
`;

const openInstagram = () => {
    const instagramUrl = 'https://instagram.com/draw_with_ninno'; // Replace with your Instagram profile URL
    const appUrl = 'instagram://user?username=draw_with_ninno'; // Replace with your Instagram username

    // Check if the screen width is greater than 900px
    if (window.innerWidth > 900) {
        window.open(instagramUrl, '_blank');
    } else {        
        window.location.href = appUrl;
    }
};

const Footer = () => {
    const theme = useTheme();

    return (
        <Box component="footer" id="footer" sx={{ py: 2, textAlign: 'center', backgroundColor: theme.palette.background.default }}>
            <Box
                sx={{
                    width: '90%',
                    mx: 'auto', // mx stands for margin-left and margin-right: auto
                    borderTop: `1px solid ${theme.palette.secondary.main}`,
                    marginBottom: '25px',
                }}
            ></Box>
            <Container maxWidth="lg">
                <Grid container spacing={4}>
                    <Grid item xs={12} md={6}>
                        <Typography variant="h6" gutterBottom>
                            Contactos
                        </Typography>
                        <List>
                            <ListItem>
                                <ListItemText primary="Na zona do Pinhal Novo" />
                            </ListItem>
                            <ListItem>
                                <ListItemText
                                    primary={
                                        <Link href="tel:+351963801658" color="inherit">
                                            Phone: (+351) 963801658
                                        </Link>
                                    }
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemText
                                    primary={
                                        <Link href="mailto:info@ninnotattoos.com" color="inherit">
                                            Email: info@ninnotattoos.com
                                        </Link>
                                    }
                                />
                            </ListItem>
                        </List>
                        <SocialIconsContainer>
                            <SocialIcon iconColor="#c32aa3">
                                <InstagramIcon onClick={openInstagram} />
                            </SocialIcon>
                        </SocialIconsContainer>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Typography variant="h6" gutterBottom>
                            Localizao
                        </Typography>
                        <Box sx={{ border: `1px solid ${theme.palette.secondary.main}`, height: '200px' }}>
                            {/* Replace the iframe src with your Google Maps link */}
                            <iframe
                                title="Google Maps"
                                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d49862.016479911545!2d-8.937900593839554!3d38.640233267525!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0xd19409858c24d63%3A0x500ebbde4910920!2sPinhal%20Novo!5e0!3m2!1spt-PT!2spt!4v1720485323947!5m2!1spt-PT!2spt"
                                width="100%"
                                height="100%"
                                frameBorder="0"
                                style={{ border: 0 }}
                                allowFullScreen=""
                                aria-hidden="false"
                                tabIndex="0"
                            ></iframe>
                        </Box>
                    </Grid>
                    {/* 
                    <Grid item xs={12} md={6}>
                        <Typography variant="h6" gutterBottom>
                            Formulrio De Contacto
                        </Typography>
                        <form noValidate autoComplete="off">
                            <TextField
                                variant="outlined"
                                margin="normal"
                                required
                                fullWidth
                                id="name"
                                label="Nome"
                                name="name"
                                autoComplete="name"
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                required
                                fullWidth
                                id="email"
                                label="Email"
                                name="email"
                                autoComplete="email"
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                required
                                fullWidth
                                id="message"
                                label="Mensagem"
                                name="message"
                                multiline
                                rows={4}
                            />
                            <Button type="submit" variant="contained" color="secondary" sx={{ mt: 2 }}>
                                Enviar
                            </Button>
                        </form>
                    </Grid>
                    */}
                </Grid>
            </Container>
        </Box>
    );
}

export default Footer;